import React, {ChangeEvent, useEffect, useState} from 'react';
import { useDispatch } from 'react-redux';
import { updateMealWeight } from "../../../mealSlice";
import {InputAdornment, TextField} from "@material-ui/core";
import MealWeightValidator from "./MealWeightValidator";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";

interface PropsType {
    weight: number;
    index: number;
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    inputField: {
        marginBottom: theme.spacing(3)
    }
}));

export default function MealWeightField({ weight, index }: PropsType) {
    const dispatch = useDispatch();
    const [error, setError] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const [localWeight, setLocalWeight] = useState("");
    const classes = useStyle();

    function updateLocalWeight(weight: number) {
        if (weight !== 0)
            setLocalWeight(weight.toString());
        else
            setLocalWeight("");
    }
    
    useEffect(() => {
        updateLocalWeight(weight)
    }, [weight]);
    
    const updateWeight = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const weight = Number(event.target.value);
        const [ isValid, errorMessage ] = MealWeightValidator.validate(weight);
        
        setError(!isValid);
        setErrorMessage(errorMessage);
        
        dispatch(updateMealWeight({newValue: weight, index: index}));
    }

    return (
        <TextField
            className={classes.inputField}
            label="Poids"
            type="number"
            value={localWeight}
            error={error}
            helperText={errorMessage}
            onChange={updateWeight}
            InputProps={{
                endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
            autoFocus={true}
        />
    );
}
