import React, {ChangeEvent} from "react";
import {Satiety} from "../../../../model/Satiety";
import {FormControl, InputLabel, MenuItem, Select} from "@material-ui/core";
import {useDispatch} from "react-redux";
import {updateSatiety} from "../../mealSlice";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";

interface PropsType {
    satiety: Satiety;
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    element: {
        marginBottom: theme.spacing(3)
    }
}));

export default function SatietySelect({satiety}: PropsType) {
    const dispatch = useDispatch();
    const classes = useStyle();
    
    const dispatchUpdateSatiety = (event: ChangeEvent<{ value: unknown }>) => {
        const feeling = event.target.value as Satiety;
        
        if (feeling != null)
            dispatch(updateSatiety(feeling))
    }
    
    return (
        <FormControl className={classes.element}>
            <InputLabel id="satietyLabel">Sensation apr√®s le repas</InputLabel>
            <Select labelId="satietyLabel" value={satiety} onChange={dispatchUpdateSatiety}>
                <MenuItem value={Satiety.stillHungry} >Encore faim</MenuItem>
                <MenuItem value={Satiety.fine}>Bien</MenuItem>
                <MenuItem value={Satiety.ateToMuch}>Trop mang√©</MenuItem>
            </Select>
        </FormControl>    
    );
}