import {MealFormState} from "./mealSlice";

export default class MealFormValidator {
    public static validate(mealState: MealFormState): [boolean, string] {
        const mealTotalWeight = mealState.weight.reduce((w1, w2) => w1 + w2);
        
        if (mealTotalWeight === 0)
            return [false, "Le poids total doit √™tre diff√©rent de 0"]
        
        return [true, ""];
    }
}