export default class MealWeightValidator {
    static validate(weight: number): [ boolean, string ] {
        
        if (weight == null) 
            return [ false, "Le poids est obligatoire" ];

        if (isNaN(weight))
            return [ false, "Le poids doit √™tre un nombre" ];
        
        if (weight === 0)
            return [false, "Le poids ne doit pas √™tre √©gale √† 0"]

        return [ true, "" ];
    }
}