import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import {store} from "./app/store";
import {Provider} from "react-redux";
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';
import Layout from "./Layout";
import Home from "./features/home/Home";
import MealForm from "./features/mealForm/MealForm";

ReactDOM.render(
    <Provider store={store}>
        <Router>
            <Layout>
                <Route path="/food-tracker/" exact component={Home} />
                <Route path="/food-tracker/add-meal" component={MealForm} />
                <Route path="/" exact>
                    <Redirect to="/food-tracker/" />
                </Route>
                <Route path="/add-meal" exact>
                    <Redirect to="/food-tracker/add-meal" />
                </Route>
            </Layout>
        </Router>
    </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

