import React from "react";
import {Button} from "@material-ui/core";
import {useDispatch} from "react-redux";
import {saveMealAsync} from "../../mealSlice";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import {NavLink} from "react-router-dom";

interface PropsType {
    disabled: boolean;
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    button: {
        marginTop: theme.spacing(3)
    }
}));

export default function SaveMealButton({ disabled }: PropsType) {
    const dispatch = useDispatch();
    const classes = useStyle();
    
    const saveMeal = () => {
        dispatch(saveMealAsync());
    }
    
    return (
      <Button disabled={disabled} variant="contained" className={classes.button} color="primary" onClick={saveMeal} component={NavLink} to="/food-tracker/">
          Enregistrer
      </Button>  
    );
}