import React from 'react';
import { useDispatch } from 'react-redux';
import { updateMealTime } from "../../mealSlice";
import DateFnsUtils from "@date-io/date-fns"
import {MuiPickersUtilsProvider, KeyboardTimePicker} from "@material-ui/pickers";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";

interface PropsType {
    time?: Date
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    datePicker: {
        marginBottom: theme.spacing(3)
    }
}));

export default function MealTimeField({ time }: PropsType) {
    const dispatch = useDispatch();
    const classes = useStyle();

    function useDateUpdate(time: Date | null) {
        if (time !== null)
            dispatch(updateMealTime(time));
    }

    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardTimePicker
                className={classes.datePicker}
                onChange={useDateUpdate}
                value={time}
                format="HH:mm"
                label="Heure du repas"
                invalidDateMessage="Format d'heure invalide"
                ampm={false}
            />
        </MuiPickersUtilsProvider>
    );
}
