import * as React from 'react';
import {
    Drawer,
    Hidden,
    List,
    ListItem,
    ListItemIcon,
    ListItemText, Toolbar, Typography
} from "@material-ui/core";
import HomeIcon from "@material-ui/icons/Home";
import AddIcon from "@material-ui/icons/Add";
import {NavLink} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {navMenuSelector, closeDrawer} from "./navMenuSlice";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import MenuIcon from "@material-ui/icons/Menu";

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        drawer: {
            width: drawerWidth,
        },
        drawerPaper: {
            width: drawerWidth,
        },
    }),
);

const drawer = (
    <React.Fragment>
        <List>
            <ListItem component={NavLink} to="/food-tracker/" key="Home">
                <ListItemIcon><HomeIcon /></ListItemIcon>
                <ListItemText primary="Accueil" />
            </ListItem>
            <ListItem component={NavLink} to="/food-tracker/add-meal" key="AddMeal">
                <ListItemIcon><AddIcon /></ListItemIcon>
                <ListItemText primary="Ajouter un repas" />
            </ListItem>
        </List>      
    </React.Fragment>
);

export default function NavMenu() {
    const navMenuState = useSelector(navMenuSelector);
    const dispatch = useDispatch();
    const classes = useStyles();
    
    return (
        <nav>
            <Hidden smUp>
                <Drawer 
                    variant="temporary" 
                    anchor="left" 
                    open={navMenuState.drawerIsOpen}
                    onClick={() => dispatch(closeDrawer())}
                    className={classes.drawer}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                >
                    <Toolbar>
                        <ListItemIcon><MenuIcon /></ListItemIcon>
                        <Typography variant="h5">
                            Menu
                        </Typography>
                    </Toolbar>
                    {drawer}
                </Drawer>
            </Hidden>
            <Hidden xsDown>
                <Drawer 
                    variant="permanent"
                    className={classes.drawer}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                >
                    <Toolbar />
                    {drawer}
                </Drawer>
            </Hidden>
        </nav>
    );
}