import React from "react";
import {Slider, Typography} from "@material-ui/core";
import { updateHungerNote } from "../../mealSlice";
import {useDispatch} from "react-redux";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";

interface PropsType {
    hungerNote: number
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    slider: {
        marginBottom: theme.spacing(3)
    }
}));

export default function HungerNoteSlider({ hungerNote }: PropsType) {
    const dispatch = useDispatch();
    const classes = useStyle();
    
    const marks = Array.from(new Array(11).keys()).map(key => ({
        value: key,
        label: key.toString()
    }));
    
    const dispatchNewHungerNote = (event: any, newValue: number | number[]) => {
        const valueAsNumber = newValue as number;
        if (valueAsNumber != null)
            dispatch(updateHungerNote(valueAsNumber));
    }
    
    return (
        <div className={classes.slider}>
            <Typography>
                Evaluation de la faim
            </Typography>
            <Slider
                aria-label="Evaluation de la faim"
                marks={marks}
                value={hungerNote}
                min={0}
                max={10}
                step={1}
                onChangeCommitted={dispatchNewHungerNote}
            />
        </div>
    )
}