import {Connection} from "jsstore";
import Meal from "../../model/Meal";
import getDbSchema from "./MealDatabaseSchema";
import MealDatabaseAdapter from "./MealDatabaseAdapter";
import GetConnection from "../ConnectionFactory";

export default class MealRepository {
    private static _instance: MealRepository | undefined;
    private static readonly MealDatabaseName = "Meals";
    private _connection: Connection;
    
    private constructor(connection: Connection) {
        this._connection = connection;
    }
    
    public async getLastMeals(nbOfMeals: number): Promise<Meal[]> {
        const mealDbObject = await this._connection.select({
            from: MealRepository.MealDatabaseName,
            order: {
                by: "date",
                type: "desc"
            },
            limit: nbOfMeals
        });
        
        return mealDbObject
            .map((dbObject) => new MealDatabaseAdapter(dbObject).Meal as Meal)
    }
    
    public async saveMeal(meal: Meal): Promise<boolean> {
        const adapter = new MealDatabaseAdapter(meal);
        const numberOfLineAffected: number | unknown = meal.id === undefined
            ? await this.insert(adapter)
            : await this.update(adapter)
        
        return typeof numberOfLineAffected === "number" && numberOfLineAffected > 0;
    }
    
    public async deleteMeals(mealIds: number[]): Promise<boolean> {
        const numberOfDeletedLines = await this._connection.remove({
            from: MealRepository.MealDatabaseName,
            where: {
                id: {
                    in: mealIds
                }
            }
        });
        
        return numberOfDeletedLines > 0;
    }

    private async insert(adapter: MealDatabaseAdapter): Promise<number | unknown> {
        return await this._connection.insert({
            into: MealRepository.MealDatabaseName,
            values: [adapter]
        });
    }
    
    private async update(adapter: MealDatabaseAdapter): Promise<number | unknown> {
        return await this._connection.update({
            in: MealRepository.MealDatabaseName,
            where: {
                id: adapter.id
            },
            set: {
                date:  adapter.date,
                weight:  adapter.weight,
                hungerNote : adapter.hungerNote,
                satiety : adapter.satiety
            }
        });
    }

    public static async getInstance() {
        if (this._instance === undefined) {
            const connection = GetConnection();
            await connection.initDb(getDbSchema(this.MealDatabaseName));
            this._instance = new MealRepository(connection);
        }
        
        return this._instance;
    }
}

