import {createSlice} from "@reduxjs/toolkit";
import {RootState} from "../../app/store";

interface NavMenuState {
    drawerIsOpen: boolean;
}

const initialState : NavMenuState = {
    drawerIsOpen: false,
}

export const navMenuSlice = createSlice({
        name: "navMenu",
        initialState,
        reducers: {
            openDrawer: state => {
                state.drawerIsOpen = true;
            },
            closeDrawer: state => {
                state.drawerIsOpen = false;
            }
        }
    }
)

export const { openDrawer, closeDrawer } = navMenuSlice.actions;

export const navMenuSelector = (state: RootState) => state.navMenu; 
    
const navMenuReducer = navMenuSlice.reducer;

export default navMenuReducer;