import Meal from "../../model/Meal";

export default  class MealDatabaseAdapter {
    private readonly _meal: Meal | undefined;
    
    readonly id: number | undefined;
    readonly date: string | undefined;
    readonly weight: string | undefined;
    readonly hungerNote: number | undefined;
    readonly satiety: number | undefined;
    
    constructor(meal: any) {
        if (meal instanceof Meal) {
            this.id = meal.id;
            this.date = meal.date.toISOString();
            this.weight = meal.weight.join(';');
            this.hungerNote = meal.hungerNote;
            this.satiety = meal.satiety;
        }
        else
            this._meal = Meal.FromValues(
                meal.id,
                meal.date,
                meal.weight.split(';').map((weight: string) => Number(weight)),
                meal.hungerNote,
                meal.satiety
            );
    }
    
    public get Meal(): Meal | undefined {
        return this._meal;
    }
}