import React from "react";
import MealWeightField from "./MealWeightField";
import {addMealWeight, removeMealWeight} from "../../mealSlice";
import {Button} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";
import {useDispatch} from "react-redux";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";

interface PropsType {
    weightValues: number[];
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    buttonSection: {
        marginBottom: theme.spacing(3),
        display: "flex",
        justifyContent: "space-evenly"
    }
}));

export default function MealWeightComponent({ weightValues }: PropsType) {
    const dispatch = useDispatch();
    const classes = useStyle();
    
    const addWeightValue = () => {
        dispatch(addMealWeight())
    }
    
    const removeWeightValue = () => {
        dispatch(removeMealWeight())
    }
    
    return (
        <React.Fragment>
            {weightValues.map((weight, index) => 
                <MealWeightField key={index} weight={weight} index={index} />
            )}
            <div className={classes.buttonSection}>
                <Button
                    variant="contained"
                    color="secondary"
                    children={<RemoveIcon />}
                    onClick={removeWeightValue}
                />
                <Button
                    variant="contained"
                    color="default"
                    children={<AddIcon />}
                    onClick={addWeightValue}
                />    
            </div>
        </React.Fragment>
    );
}