import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {Satiety} from "../../model/Satiety";
import {AppThunk, RootState} from "../../app/store";
import Meal from "../../model/Meal";
import MealRepository from "../../repositories/MealRepository";

export interface MealFormState {
    id: number | undefined;
    date: string;
    weight: number[];
    hungerNote: number;
    satiety: Satiety;
}

const initialState: MealFormState = {
    id: undefined,
    date: new Date(Date.now()).toISOString(),
    weight: [0],
    hungerNote: 0,
    satiety: Satiety.fine
};

export const mealSlice = createSlice({
    name: "mealForm",
    initialState,
    reducers: {
        updateMealDate: (state, action: PayloadAction<string>) => {
            const dateInState = new Date(state.date);
            const newDate = new Date(action.payload);

            newDate.setHours(dateInState.getHours());
            newDate.setMinutes(dateInState.getMinutes());
            
            state.date = newDate.toISOString()
        },
        updateMealTime: (state, action: PayloadAction<string>) => {
            const dateInState = new Date(state.date);
            const newDate = new Date(action.payload);
            
            newDate.setFullYear(dateInState.getFullYear());
            newDate.setMonth(dateInState.getMonth());
            newDate.setDate(dateInState.getDate());
            
            state.date = newDate.toISOString()
        },
        updateMealWeight: (state, action: PayloadAction<{ newValue: number, index: number }>) => {
            state.weight[action.payload.index] = action.payload.newValue
        },
        addMealWeight: state => {
            state.weight.push(0);
        },
        removeMealWeight: state => {
            state.weight.pop();
        },
        updateSatiety: (state, action: PayloadAction<Satiety>) => {
            state.satiety = action.payload;
        },
        updateHungerNote: (state, action: PayloadAction<number>) => {
            state.hungerNote = action.payload;
        },
        clearState: state => initialState
    }
})

export const { updateHungerNote, updateSatiety, updateMealWeight, addMealWeight, removeMealWeight} = mealSlice.actions

export const updateMealDate = (date: Date) =>
    mealSlice.actions.updateMealDate(date.toISOString());

export const updateMealTime = (date: Date) =>
    mealSlice.actions.updateMealTime(date.toISOString());

export const saveMealAsync = (): AppThunk => async (dispatch, getState) => {
    const mealState = getState().mealForm;
    const mealToSave = Meal.FromValues(mealState.id, mealState.date, mealState.weight, mealState.hungerNote, mealState.satiety);
    const mealRepository = await MealRepository.getInstance()
    const success = await mealRepository.saveMeal(mealToSave);

    if (success)
        dispatch(mealSlice.actions.clearState());
};

export const addMealFormSelector = (state: RootState) => state.mealForm;

const mealFormReducer = mealSlice.reducer;
export default mealFormReducer;