import React, {useEffect, useState} from "react";
import { useSelector } from 'react-redux';
import { addMealFormSelector } from "./mealSlice";
import MealDateField from "./components/MealDateField";
import MealTimeField from "./components/MealTimeField";
import HungerNoteSlider from "./components/HungerNoteSlider";
import SatietySelect from "./components/SatietySelect";
import {FormControl} from "@material-ui/core";
import MealWeightComponent from "./components/MealWeightComponent";
import SaveMealButton from "./components/SaveMealButton";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import TotalWeight from "./components/TotalWeight";
import MealFormValidator from "./MealFormValidator";

const useStyle = makeStyles((theme: Theme) => createStyles({
  container: {
    width: "100%",
    display: "flex",
    justifyContent: "center"
  }
}));

export default function MealForm() {
  const state = useSelector(addMealFormSelector);
  const [mealIsValid, setMealIsValid] = useState(false);
  const classes = useStyle();
  
  useEffect(() => {
    const [isMealValid] = MealFormValidator.validate(state);
    setMealIsValid(isMealValid)
  }, [state])
  
  return (
    <div className={classes.container}>
      <FormControl>
        <TotalWeight weightValues={state.weight} />
        <MealWeightComponent weightValues={state.weight} />
        <MealDateField date={new Date(state.date)} />
        <MealTimeField time={new Date(state.date)} />
        <HungerNoteSlider hungerNote={state.hungerNote} />
        <SatietySelect satiety={state.satiety} />
        <SaveMealButton disabled={!mealIsValid} />
      </FormControl>
    </div>
  );
}