import React from "react";
import Meal from "../../../../model/Meal";
import {TableCell, TableRow} from "@material-ui/core";
import MealDateTime from "./components/MealDateTime";
import MealWeight from "./components/MealWeight/MealWeight";
import HungerNote from "./components/HungerNote";
import SatietyLabel from "./components/SatietyLabel/Satiety";
import {useDispatch} from "react-redux";
import {addSelectedMealId, removeSelectedMealId} from "../../homeSlice";

interface PropsType {
    meal: Meal;
    isSelected: boolean;
}

export default function MealRow({ meal, isSelected }: PropsType) {
    const dispatch = useDispatch();
    
    const toggleSelection = () => {
        if (isSelected)
            dispatch(removeSelectedMealId(Number(meal.id)));
        else
            dispatch(addSelectedMealId(Number(meal.id)));
    }
    
    return (
        <TableRow onClick={toggleSelection} key={meal.id} selected={isSelected}>
            <TableCell>
                <MealDateTime date={meal.date} />
            </TableCell>
            <TableCell align="right">
                <MealWeight weightValues={meal.weight} />
            </TableCell>
            <TableCell>
                <HungerNote note={meal.hungerNote} />
            </TableCell>
            <TableCell>
                <SatietyLabel satiety={meal.satiety} />
            </TableCell>
        </TableRow>
    );
}