import React from "react";
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography} from "@material-ui/core";
import MealRow from "./components/MealRow/MealRow";
import {useDispatch, useSelector} from "react-redux";
import {homeSelector, loadLastMeals} from "./homeSlice";
import DeleteMealsButton from "./components/DeleteButton";
import Meal from "../../model/Meal";

export default function Home() {
    const dispatch = useDispatch();
    dispatch(loadLastMeals(10));
    const homeState = useSelector(homeSelector);
    
    return (
        <div>
            <Typography variant="h4">
                Derniers repas
            </Typography>
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Date et heure</TableCell>
                            <TableCell>Poids</TableCell>
                            <TableCell>Faim</TableCell>
                            <TableCell>Sati√©t√©</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {homeState.lastMeals.map(serializedMeal => {
                            const meal = Meal.parse(serializedMeal);
                            return (<MealRow key={meal.id} meal={meal} isSelected={homeState.selectedMealIds.includes(Number(meal.id))}/>)
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            <DeleteMealsButton mealIds={homeState.selectedMealIds} />
        </div>
    )
}