import React from "react";
import {Typography} from "@material-ui/core";

interface PropsType {
    date: Date;
}

export default function MealDateTime({ date }: PropsType) {
    function formatNumber(value: number): string {
        return value.toString().padStart(2, '0');
    }
    
    const formattedDate = `${formatNumber(date.getDate())}/${formatNumber(date.getMonth())}/${formatNumber(date.getFullYear())}`;
    const formattedTime = `${formatNumber(date.getHours())}:${formatNumber(date.getMinutes())}`;
    return (
        <Typography variant="body1">{formattedDate} {formattedTime}</Typography>
    );
}