import {Satiety} from "./Satiety";

export default class Meal {
    private readonly _id: number | undefined;
    private readonly _date: Date;
    private readonly _weight: number[];
    private readonly _hungerNote: number;
    private readonly _satiety: Satiety;
    
    private constructor(id: number | undefined, date: Date, weightValues: number[], hungerNote: number, satiety: Satiety) {
        this._id = id;
        this._hungerNote = hungerNote;
        this._satiety = satiety;
        this._date = new Date(date);
        this._weight = Meal.filterNullValues(weightValues)
    }
    
    private static filterNullValues(weightValues: number[]): number[] {
        const filteredWeightValues = weightValues.filter(value => value > 0);
        return filteredWeightValues.length > 0
            ? filteredWeightValues
            : [0];
    }
    
    public get id(): number | undefined {
        return this._id;
    }

    public get date(): Date {
        return this._date;
    }

    public get weight(): number[] {
        return this._weight;
    }

    public get hungerNote(): number {
        return this._hungerNote;
    }

    public get satiety(): Satiety {
        return this._satiety;
    }
    
    public get totalWeight() {
        return this._weight.reduce((w1, w2) => w1 + w2);
    }
    
    public toString(): string {
        return JSON.stringify(this);
    }
    
    static FromValues(id: number | undefined, date: string, weight: number[], hungerNote: number, satiety: Satiety): Meal {
        return new Meal(
            id,
            new Date(date),
            weight,
            hungerNote,
            satiety
        );
    }
    
    static parse(serializedMeal: string): Meal {
        const deserialized = JSON.parse(serializedMeal);
        return new Meal(
            deserialized._id,
            new Date(deserialized._date),
            deserialized._weight,
            deserialized._hungerNote,
            deserialized._satiety
        )
    }
}