import React from 'react';
import { useDispatch } from 'react-redux';
import { updateMealDate } from "../../mealSlice";
import DateFnsUtils from "@date-io/date-fns"
import {MuiPickersUtilsProvider, KeyboardDatePicker} from "@material-ui/pickers";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";

interface PropsType {
    date?: Date
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    datePicker: {
        marginBottom: theme.spacing(3)
    }
}));

export default function MealDateField({ date }: PropsType) {
    const dispatch = useDispatch();
    const classes = useStyle();
    
    function useDateUpdate(date: Date | null) {
        if (date !== null)
            dispatch(updateMealDate(date));
    }
    
    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
                className={classes.datePicker}
                onChange={useDateUpdate}
                value={date}
                format="dd/MM/yyyy"
                label="Date du repas"
                invalidDateMessage="Format de date invalide"
            />
        </MuiPickersUtilsProvider>
    );
}
