import {Typography} from "@material-ui/core";
import React from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";

interface PropsType {
    weightValues: number[];
}

const useStyle = makeStyles((theme: Theme) => createStyles({
    totalWeight: {
        marginBottom: theme.spacing(3)
    }
}));

export default function TotalWeight({ weightValues }: PropsType) {
    const totalWeight = weightValues.reduce((w1, w2) => w1 + w2);
    const classes = useStyle();
    
    return (
        <Typography className={classes.totalWeight} variant="h6">Poids total : {totalWeight}</Typography>
    )
}