export default function getDbSchema(mealDatabaseName: string) {
    const mealSchema = {
        name: mealDatabaseName,
        columns: {
            id: { primaryKey: true, autoIncrement: true },
            date:  { notNull: true, dataType: "string" },
            weight:  { notNull: true, dataType: "string" },
            hungerNote : { notNull: true, dataType: "number" },
            satiety : { notNull: true, dataType: "number" }
        }
    }

    return {
        name: "mealDatabase",
        tables: [mealSchema]
    };
}