import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {AppThunk, RootState} from "../../app/store";
import MealRepository from "../../repositories/MealRepository";

export interface HomeState {
    lastMeals: string[];
    selectedMealIds: number[]
}

const initialState: HomeState = {
    lastMeals: [],
    selectedMealIds: []
}

const homeSlice = createSlice({
    name: "home",
    initialState,
    reducers: {
        addSelectedMealId: (state, action: PayloadAction<number>) => {
            state.selectedMealIds = [...state.selectedMealIds, action.payload]
        },
        removeSelectedMealId: (state, action: PayloadAction<number>) => {
            state.selectedMealIds = state.selectedMealIds.filter(id => id !== action.payload);
        },
        lastMealsLoaded: (state, action: PayloadAction<string[]>) => {
            state.lastMeals = action.payload;
        }
    }
});

export const loadLastMeals = (numberOfMealToLoad: number): AppThunk => async dispatch => {
    const repository = await MealRepository.getInstance();
    const lastMeals = await repository.getLastMeals(numberOfMealToLoad);
    dispatch(homeSlice.actions.lastMealsLoaded(lastMeals.map(meal => meal.toString())));
}

export const deleteMealsAsync = (mealIds: number[]): AppThunk => async dispatch => {
    const repository = await MealRepository.getInstance();
    const success = await  repository.deleteMeals(mealIds);
    
    if (success)
        mealIds.map(id => dispatch(removeSelectedMealId(id)));
}

export const {addSelectedMealId, removeSelectedMealId } = homeSlice.actions;

export const homeSelector = (state: RootState) => state.home;

const homeReducer = homeSlice.reducer;
export default homeReducer;